"use client";
import Link from "next/link";
import { clearAuth, getAuth } from "../lib/auth";
import { useEffect, useState } from "react";
import { usePathname } from "next/navigation";

export default function Nav() {
  const pathname = usePathname();
  const [auth, setAuth] = useState<{ uid: string; token: string } | null>(null);

  // Read auth only on the client after mount to avoid SSR / hydration mismatch
  useEffect(() => {
    setAuth(getAuth());
  }, []);

  return (
    <div className="nav">
      <div className="nav-inner">
        <div className="nav-brand">
          <span className="pill">Main St AI</span>
          <span className="muted">Business OS</span>
        </div>
        <div className="nav-links">
          <Link className={`nav-link ${pathname === "/dashboard" ? "active" : ""}`} href="/dashboard">
            Dashboard
          </Link>
          <Link className={`nav-link ${pathname === "/profile" ? "active" : ""}`} href="/profile">
            Profile
          </Link>
          <Link className={`nav-link ${pathname === "/contacts" ? "active" : ""}`} href="/contacts">
            Contacts
          </Link>
          <Link className={`nav-link ${pathname === "/chat" ? "active" : ""}`} href="/chat">
            Chat
          </Link>
          <Link className={`nav-link ${pathname === "/owner-cover" ? "active" : ""}`} href="/owner-cover">
            Owner Cover
          </Link>
          <Link className={`nav-link ${pathname === "/decision-feed" ? "active" : ""}`} href="/decision-feed">
            Decisions
          </Link>
          <Link className={`nav-link ${pathname === "/action-queue" ? "active" : ""}`} href="/action-queue">
            Action Queue
          </Link>
          <Link className={`nav-link ${pathname === "/memory" ? "active" : ""}`} href="/memory">
            Memory
          </Link>
          <Link className={`nav-link ${pathname === "/outcomes" ? "active" : ""}`} href="/outcomes">
            Outcomes
          </Link>
          <Link className={`nav-link ${pathname === "/how-it-works" ? "active" : ""}`} href="/how-it-works">
            How It Works
          </Link>
          {auth ? (
            <button
              className="btn ghost"
              onClick={() => {
                clearAuth();
                location.href = "/login";
              }}
            >
              Logout
            </button>
          ) : (
            <Link className={`nav-link ${pathname === "/login" ? "active" : ""}`} href="/login">
              Login
            </Link>
          )}
        </div>
      </div>
    </div>
  );
}
